	module.exports = {"data":{"markdownRemark":{"html":"<p>In this episode I talk about game makers and why they might be a good option to help you start building learning experiences.</p>\n<iframe src=\"https://simplecast.com/e/52594?style=medium-light\" width=\"100%\" height=\"94px\" frameborder=\"0\" scrolling=\"no\" seamless=\"\"></iframe>\n<p>When I say game makers, I mean tools that democratize making (mostly 2D) games. This broad category of tools can be less expensive, more versatile, and still allow offer you the same experience in creating effective learning experiences. With the recent announcement about Articulate 360, while exciting, I got to thinking that this episode may be even more relevant for other ID newbies who may feel priced out of that new platform. Like the last episode, this is a great episode for people looking for an alternative way to get starting creating interactive learning experiences.</p>","frontmatter":{"date":"December 01, 2016","path":"/blog/dearid-game-makers","title":"#DearID: Game Makers for Learning","tags":["elearning development","game dev","game development","learning experience design"],"categories":["Dear ID Show","GameDev"],"featured":{"childImageSharp":{"sizes":{"base64":"data:image/png;base64,","aspectRatio":1,"src":"/static/Game-Makers-for-Learning-1e39a851d9bc4778a53220cc4e6ba3c0-81d39.png","srcSet":"/static/Game-Makers-for-Learning-1e39a851d9bc4778a53220cc4e6ba3c0-9a8d1.png 203w,\n/static/Game-Makers-for-Learning-1e39a851d9bc4778a53220cc4e6ba3c0-c53f9.png 405w,\n/static/Game-Makers-for-Learning-1e39a851d9bc4778a53220cc4e6ba3c0-81d39.png 810w,\n/static/Game-Makers-for-Learning-1e39a851d9bc4778a53220cc4e6ba3c0-e460a.png 1215w,\n/static/Game-Makers-for-Learning-1e39a851d9bc4778a53220cc4e6ba3c0-f34da.png 1400w","sizes":"(max-width: 810px) 100vw, 810px"}}}}}},"pathContext":{}}

